export const createGenAPI = (http) => {
    return {
        genGenNamesByModelID(modelID) {
            return http.get('/data/generation', { params: { modelID } });
        },
        getGen(genID) {
            return http.get(`/data/generation/${genID}`);
        },
        postGen(data) {
            return http.post(`/data/generation/`, data);
        },
        patchGen(genID, data) {
            return http.patch(`/data/generation/${genID}`, data);
        }
    };
};
