import { LOAD_ERROR } from 'src/api/error';
import { restore, store } from 'src/util/hashStore';
import { get, writable } from 'svelte/store';
const DEFAULT_STATE = {
    map: {},
    error: null,
    warn: null,
    loading: false,
};
export var DATA_PARAMS;
(function (DATA_PARAMS) {
    DATA_PARAMS["TOTAL"] = "total";
    DATA_PARAMS["NORMALIZE"] = "norm";
    DATA_PARAMS["BY_AGE"] = "by_age";
    DATA_PARAMS["BY_MILEAGE"] = "by_mileage";
})(DATA_PARAMS || (DATA_PARAMS = {}));
;
export const createDefectStore = (api) => {
    const onerror = () => { setState({ error: LOAD_ERROR }); };
    const loadParams = createLoadParams();
    const { categories, setCategories, getCategories } = createCategoriesStore(api, onerror);
    const state = writable(Object.assign({}, DEFAULT_STATE));
    const dataParams = writable({
        [DATA_PARAMS.TOTAL]: false,
        [DATA_PARAMS.NORMALIZE]: true,
        [DATA_PARAMS.BY_AGE]: true,
        [DATA_PARAMS.BY_MILEAGE]: false,
    });
    dataParams.subscribe(updateAllData);
    categories.subscribe(updateAllData);
    function updateAllData() {
        setState({ loading: true });
        Promise
            .all(loadParams.get().map(([name, p]) => req(name, p, getCategories())))
            .then((results) => { updateData(Object.assign({}, ...results)); })
            .catch(onerror)
            .then(() => { setState({ loading: false }); });
    }
    function updateData(data) {
        state.update((prev) => {
            return Object.assign({}, prev, {
                map: Object.assign({}, prev.map, data),
                error: null
            });
        });
    }
    function req(name, entity, categories) {
        const params = Object.assign(Object.assign({}, entity), get(dataParams));
        return (get(dataParams).by_mileage ?
            api.getDefectsByMileage :
            api.getDefectsByAge)(Object.assign(Object.assign({}, params), { categories }))
            .then(({ data, status }) => {
            if (status != 200) {
                throw LOAD_ERROR;
            }
            loadParams.set(name, params);
            if (Object.keys(data).length === 0) {
                setState({ warn: 'warn.NO_DATA' });
                setTimeout(() => { setState({ warn: null }); }, 3000);
                return;
            }
            return { [name]: data };
        });
    }
    function setState(values) {
        state.update((s) => (Object.assign(Object.assign({}, s), values)));
    }
    ;
    return {
        state,
        dataParams,
        categories,
        setDataParams(cfg) {
            dataParams.update((prev) => (Object.assign(Object.assign({}, prev), cfg)));
        },
        setCategories,
        loadDefects(name, entity) {
            setState({ loading: true });
            req(name, entity, getCategories())
                .then((res) => {
                res && updateData(res);
                setState({ loading: false });
            })
                .catch((error) => { setState({ error, loading: false }); });
        },
    };
};
function createLoadParams() {
    const HASH_KEY = 'load';
    let loadParams = {};
    try {
        loadParams = JSON.parse(restore(HASH_KEY));
    }
    catch (e) {
        loadParams = {};
    }
    return {
        get() {
            return Object.entries(loadParams);
        },
        set(name, params) {
            loadParams[name] = params;
            store(HASH_KEY, JSON.stringify(loadParams));
        }
    };
}
function createCategoriesStore(api, onerror) {
    const HASH_KEY = 'defect_categories';
    const SEPARATOR = ',';
    const categories = writable([]);
    categories.subscribe(() => {
        const cats = getCategories();
        if (cats) {
            store(HASH_KEY, getCategories());
        }
    });
    api.getDefectsCategories()
        .then((res) => {
        const selected = restore(HASH_KEY).split(SEPARATOR);
        categories.set(Object.entries(res.data).map(([value, label]) => ({ value, label, selected: selected.includes(value) })));
    })
        .catch(onerror);
    function getCategories() {
        const cats = get(categories);
        const selected = cats.filter((c) => c.selected).map((c) => c.value);
        return (selected.length ? selected : cats.map((c) => c.value)).join(SEPARATOR);
    }
    return {
        categories,
        setCategories(v) {
            categories.update((cats) => cats.map((c) => (Object.assign(Object.assign({}, c), { selected: v[c.value] }))));
        },
        getCategories
    };
}
