import { restore, store } from 'src/util/hashStore';
import { dictionary, locale } from 'svelte-i18n';
import { writable } from 'svelte/store';
import { dictionaries } from '../i18n';
const DEFAUL_LOCALE = 'en';
const HASH_KEY = 'locale';
export const creatLocaleStore = () => {
    const locales = writable([{
            label: '🇬🇧',
            selected: false,
            value: 'en',
        }, {
            label: '🇷🇺',
            selected: false,
            value: 'ru',
        }]);
    dictionary.set(dictionaries);
    locale.set(restore(HASH_KEY) || DEFAUL_LOCALE);
    locale.subscribe((v) => {
        locales.update(ls => ls.map((l) => (Object.assign(Object.assign({}, l), { selected: l.value === v }))));
        store(HASH_KEY, v || DEFAUL_LOCALE);
    });
    return {
        locales,
        select(v) {
            locale.set(v);
        }
    };
};
