export const createDefectAPI = (http) => {
    return {
        getDefectsCategories() {
            return http.get('/data/defect/categories');
        },
        getDefectsByAge(params) {
            return http.get('/data/defect/age', { params });
        },
        getDefectsByMileage(params) {
            return http.get('/data/defect/mileage', { params });
        },
    };
};
