import { LOAD_ERROR } from 'src/api/error';
import { writable } from 'svelte/store';
const DEFAULT_STATE = {
    map: {},
    error: null
};
export const createModelStore = (api) => {
    const state = writable(Object.assign({}, DEFAULT_STATE));
    const setState = (values) => {
        state.update((s) => (Object.assign(Object.assign({}, s), values)));
    };
    const loadNames = (brandID) => {
        api.genModelNamesByBrandID(brandID).then(({ data, status }) => {
            if (status != 200) {
                throw LOAD_ERROR;
            }
            setState({ map: data });
        }).catch((error) => {
            setState({ error: LOAD_ERROR });
        });
    };
    return { state, loadNames };
};
