export const createTransAPI = (http) => {
    return {
        getTransNames(brandID) {
            return http.get('/data/transmission', { params: { brandID } });
        },
        getTrans(transID) {
            return http.get(`/data/transmission/${transID}`);
        },
        postTrans(data) {
            return http.post(`/data/transmission/`, data);
        },
        patchTrans(id, data) {
            return http.patch(`/data/transmission/${id}`, data);
        },
    };
};
