<script lang="ts">
    import Search from '../components/Search.svelte';
    import { searchStore } from 'src/store/main.store';

    import { createEventDispatcher } from 'svelte';
    import { _ } from 'svelte-i18n';
    import Button from '../components/Button.svelte';

    let suggestions: { brandID?: number; modelID?: number; genID?: number; title: string; label: string }[] = [];
    $: {
        let { brands, models, gens, error } = $state;
        if (error) {
            suggestions = [];
        }
        suggestions = [].concat(
            Object.entries(brands).map(([brandID, title]) => ({
                title,
                brandID: Number(brandID),
                label: $_('label.brand'),
            })),
            Object.entries(models).map(([modelID, title]) => ({
                title,
                modelID: Number(modelID),
                label: $_('label.model'),
            })),
            Object.entries(gens).map(([genID, title]) => ({
                title,
                genID: Number(genID),
                label: $_('label.gen'),
            }))
        );
    }

    const dispatch = createEventDispatcher();

    $: ({ state } = searchStore);

    const onSearch = ({ detail }) => {
        searchStore.search(detail);
    };
    const onAddData = () => {
        dispatch('add_data');
    };
</script>

<Search {suggestions} on:change={onSearch} on:input />
<!-- <Button variant="secondary" on:click={onAddData}>{$_('label.add_data')}</Button> -->
