import axios from 'axios';
import { createBrandAPI } from './data/brand.api';
import { createDefectAPI } from './data/defect.api';
import { createEngineAPI } from './data/engine.api';
import { createGenAPI } from './data/gen.api';
import { createModelAPI } from './data/model.api';
import { createSearchAPI } from './data/search.api';
import { createTransAPI } from './data/trans.api';
import { createVersionAPI } from './data/version.api';
// !FIXME
const baseURL = 'http://localhost:8081';
const http = axios.create({
    baseURL,
    headers: {
        'Accept': 'application/json',
        'Content-Type': 'application/json',
        'Access-Control-Allow-Origin': '*', // !FIXME
    },
    transformRequest: [
        (data) => JSON.stringify(data)
    ],
    transformResponse: [
        (data, header) => header && header['content-type'] === 'application/json; charset=utf-8' && data ? JSON.parse(data) : data,
    ],
});
// http.interceptors.response.use<object>(({ data }) => data, (error) => {
//    if (error.response) {
//       if (error.response.data && error.response.data.error) {
//          return Promise.reject(error.response.data.error);
//       }
//       return Promise.reject(error.response);
//    }
//    return Promise.reject(error);
// });
export const API = {
    brand: createBrandAPI(http),
    model: createModelAPI(http),
    gen: createGenAPI(http),
    version: createVersionAPI(http),
    engine: createEngineAPI(http),
    trans: createTransAPI(http),
    defect: createDefectAPI(http),
    search: createSearchAPI(http),
};
