import { LOAD_ERROR } from 'src/api/error';
import { writable } from 'svelte/store';
const DEFAULT_STATE = {
    selected: null,
    map: {},
    error: null
};
export const createGenlStore = (api) => {
    const state = writable(Object.assign({}, DEFAULT_STATE));
    const setState = (values) => {
        state.update((s) => (Object.assign(Object.assign({}, s), values)));
    };
    const loadNames = (modelID) => {
        api.genGenNamesByModelID(modelID).then(({ data, status }) => {
            if (status != 200) {
                throw LOAD_ERROR;
            }
            setState({ map: data });
        }).catch((error) => {
            setState({ error: LOAD_ERROR });
        });
    };
    const onError = (error) => { setState({ error: LOAD_ERROR }); };
    const getData = (genID) => {
        return api.getGen(genID).then(({ data, status }) => {
            if (status != 200) {
                throw LOAD_ERROR;
            }
            setState({ selected: data });
        }).catch(onError);
    };
    const postData = (data) => {
        return api.postGen(data).catch(onError);
    };
    const patchData = (id, data) => {
        return api.patchGen(id, data).catch(onError);
    };
    return { state, loadNames, getData, postData, patchData };
};
