import { LOAD_ERROR } from 'src/api/error';
import { writable } from 'svelte/store';
const DEFAULT_STATE = {
    selected: null,
    map: {},
    error: null
};
export const createTransStore = (api) => {
    const state = writable(Object.assign({}, DEFAULT_STATE));
    const setState = (values) => {
        state.update((s) => (Object.assign(Object.assign({}, s), values)));
    };
    const loadNames = (brandID) => {
        api.getTransNames(brandID).then(({ data, status }) => {
            if (status != 200) {
                throw LOAD_ERROR;
            }
            setState({ map: data });
        }).catch(onError);
    };
    const onError = (error) => { setState({ error: LOAD_ERROR }); };
    const getData = (id) => {
        return api.getTrans(id).then(({ data, status }) => {
            if (status != 200) {
                throw LOAD_ERROR;
            }
            setState({ selected: data });
        }).catch(onError);
    };
    const postData = (data) => {
        return api.postTrans(data).catch(onError);
    };
    const patchData = (id, data) => {
        return api.patchTrans(id, data).catch(onError);
    };
    return { state, loadNames, getData, patchData, postData };
};
