export const createEngineAPI = (http) => {
    return {
        getEngineNames() {
            return http.get('/data/engine');
        },
        getEngine(engineID) {
            return http.get(`/data/engine/${engineID}`);
        },
        postEngine(data) {
            return http.post(`/data/engine/`, data);
        },
        patchEngine(id, data) {
            return http.post(`/data/engine/${id}`, data);
        },
    };
};
