<script lang="ts">
    import { createEventDispatcher } from 'svelte';
    import { _ } from 'svelte-i18n';
    import Button from './Button.svelte';
    export let variants: { label: string; selected?: boolean; value: string; color?: string }[] = [];
    export let multiselect: boolean = false;
    export let column: boolean = false;

    let isApplied = true;

    $: if (multiselect && variants.every((v) => !v.selected)) {
        variants = variants.map((v) => ({ ...v, selected: true }));
    }

    $: needApplyButton = variants.length > 2;

    const dispatch = createEventDispatcher();
    const selectOne = (value: string) => {
        isApplied = false;
        variants = variants.map((v) => ({
            ...v,
            selected: v.value === value,
        }));
        !needApplyButton && apply();
    };
    const onselect = multiselect
        ? (value: string) => {
              isApplied = false;
              variants = variants.map((v) =>
                  v.value === value
                      ? {
                            ...v,
                            selected: v.value === value && !v.selected,
                        }
                      : v
              );
              !needApplyButton && apply();
          }
        : selectOne;
    const apply = () => {
        dispatch('select', Object.fromEntries(variants.map((v) => [v.value, v.selected])));
        isApplied = true;
    };
</script>

<div class="pure-menu" class:pure-menu-horizontal={!column}>
    <ul class="pure-menu-list">
        {#each variants as v}
            <li
                class="pure-menu-item"
                class:pure-menu-selected={v.selected}
                on:click={() => onselect(v.value)}
                on:dblclick={() => selectOne(v.value)}
            >
                <span class="pure-menu-link" style={`${v.selected && v.color ? 'background-color: ' + v.color : ''}`}
                    >{$_(v.label)}</span
                >
            </li>
        {/each}
        {#if needApplyButton}
            <Button variant={isApplied ? 'secondary' : 'success'} on:click={() => apply()}
                >{isApplied ? $_('label.applied') : $_('label.apply')}</Button
            >
        {/if}
    </ul>
</div>

<style scoped>
    .pure-menu-horizontal {
        width: auto;
    }
    .pure-menu {
        cursor: pointer;
    }
    .pure-menu-link {
        box-sizing: border-box;
        transition: all 0.3s;
        background-color: inherit;
    }
    .pure-menu-link:hover {
        color: #000;
        background-color: #fff;
    }
    .pure-menu-item.pure-menu-selected .pure-menu-link {
        border-bottom: 1px solid #b5caac;
        background-color: #d6eecb;
    }
</style>
