import { LOAD_ERROR } from 'src/api/error';
import { writable } from 'svelte/store';
const DEFAULT_STATE = {
    brands: {},
    models: {},
    gens: {},
    error: null
};
export const createSearchStore = (api) => {
    const state = writable(Object.assign({}, DEFAULT_STATE));
    const setState = (values) => {
        state.update((s) => (Object.assign(Object.assign({}, s), values)));
    };
    const search = (query) => {
        api.search(query).then(({ data, status }) => {
            if (status != 200) {
                throw LOAD_ERROR;
            }
            setState(data);
        }).catch((e) => {
            setState({ error: new Error(e.message) });
        });
    };
    return {
        search, state
    };
};
