export const store = (key, data) => {
    const params = new URLSearchParams(location.hash.slice(1));
    params.set(key, data);
    location.hash = params.toString();
};
export const restore = (key) => {
    const params = new URLSearchParams(location.hash.slice(1));
    return Object.fromEntries(params.entries())[key] || '';
};
