<script lang="ts">
   import { createEventDispatcher } from 'svelte';
   import { _ } from 'svelte-i18n';

   import Button from '../../components/Button.svelte';
   export let displayUpdate = false;
   export let updateText: string;
   export let displayCreate = false;

   const dispatch = createEventDispatcher();
   const update = () => {
      dispatch('update');
   };
   const create = () => {
      dispatch('create');
   };
</script>

<div class="row">
   {#if displayUpdate}
      <Button variant="success" on:click={update}>{$_('label.update')}&nbsp;{updateText}</Button>
   {/if}
   {#if displayCreate}
      <Button variant="primary" on:click={create}>{$_('label.create')}</Button>
   {/if}
</div>

<style scoped>
   .row {
      display: flex;
   }
   :global(.row > *) {
      flex: 1;
   }
</style>
