<script lang="ts">
   import { createEventDispatcher, onDestroy, onMount } from 'svelte';
   import Button from './Button.svelte';
   import { _ } from 'svelte-i18n';
   import Input from './Input.svelte';

   export let value: string = '';
   export let suggestions: {
      brandID?: number;
      modelID?: number;
      genID?: number;
      versionID?: number;
      title: string;
      label: string;
   }[] = [];

   const dispatcher = createEventDispatcher();
   const onSuggestionClick = (s) => {
      dispatcher('input', s);
      value = s.title;
      onChange();
   };
   const onChange = () => {
      dispatcher('change', value);
   };

   const onInput = () => {
      dispatcher('input', value);
   };
</script>

<div class="Search">
   <Input type="search" bind:value placeholder="Toyota Camry xv70" on:input={onChange} {suggestions} lower>
      <div slot="suggestion_item" let:item on:click={() => onSuggestionClick(item)}>
         <span class="Search__dropdown__item__title"> {item.title}</span>
         <span class="Search__dropdown__item__label"> {item.label}</span>
      </div>
   </Input>
</div>

<style scoped>
   .Search {
   }
   .Search__dropdown__item__label {
      color: #ccc;
   }
</style>
