<script lang="ts">
   import type { IEngineData } from 'src/api/data/engine.api';
   import type { IGenData } from 'src/api/data/gen.api';
   import type { ITransData } from 'src/api/data/trans.api';
   import type { IVersionData } from 'src/api/data/version.api';
   import { _ } from 'svelte-i18n';
   import Button from '../components/Button.svelte';
   import Brand from './AddData/Brand.svelte';
   import Engine from './AddData/Engine.svelte';
   import Gen from './AddData/Gen.svelte';
   import Model from './AddData/Model.svelte';
   import Trans from './AddData/Trans.svelte';
   import Version from './AddData/Version.svelte';

   let brandID: number = 0;
   let brandName: string = '';
   let modelID: number = 0;
   let modelName: string = '';
   let genID: number = 0;
   let genData: IGenData = { Name: '', Img: '', Start: '', Finish: '' };
