   let engineData: IEngineData = {
      Name: '',
      Displacement: '',
      Cylinders: '',
      Valves: '',
      Fuel_type: '',
      Power_hp: '',
      Torque: '',
      Img: '',
   };
   let transData: ITransData = {
      Name: '',
      Gears: 0,
      Consumtion: 0,
      Acceleration: 0,
   };

   $: console.debug('brand', brandID);
   $: console.debug('model', modelID);
   $: console.debug('genID', genID, 'genData', genData);
   $: console.debug('versionID', versionID, 'versionData', versionData);
</script>

