export const createVersionAPI = (http) => {
    return {
        getVersionNamesByGenID(genID) {
            return http.get('/data/version', { params: { genID } });
        },
        getVersion(versionID) {
            return http.get(`/data/version/${versionID}`);
        },
        postVersion(data) {
            return http.post(`/data/version/`, data);
        },
        patchVersion(id, data) {
            return http.post(`/data/version/${id}`, data);
        },
    };
};
