import { restore, store } from 'src/util/hashStore';
import { writable } from 'svelte/store';
export const ROUTE_NAMES = {
    MAIN: "",
    ADD_DATA: "add_new_data",
    LOADING: "loading",
};
const HASH_KEY = 'route';
export const createNav = () => {
    const currentRouteName = writable(restore(HASH_KEY) || ROUTE_NAMES.MAIN);
    currentRouteName.subscribe((v) => {
        store(HASH_KEY, v || ROUTE_NAMES.MAIN);
    });
    const displayAddDataForm = () => {
        currentRouteName.set(ROUTE_NAMES.ADD_DATA);
    };
    return {
        displayAddDataForm,
        currentRouteName
    };
};
